import 'lazysizes';
import { ArticlesModule } from './modules/ArticlesModule/ArticlesModule';
import { ContactModule } from './modules/ContactModule/ContactModule';
import { FooterModule } from './modules/FooterModule/FooterModule';
import { MarkdownModule } from './modules/MarkdownModule/MarkdownModule';
import { PartnersModule } from './modules/PartnersModule/PartnersModule';
import { TeamModule } from './modules/TeamModule/TeamModule';
import { AffiliatesModule } from './modules/AffiliatesModule/AffiliatesModule';
import { HomeTemplate } from './templates/HomeTemplate';
import { WindowManager } from './utils/WindowManager';
import { Globals } from './utils/Globals';
import { TemplateManager } from '../lib/com/hellomonday/templateManager/TemplateManager';
import { ModuleFactory } from '../lib/com/hellomonday/templateManager/ModuleFactory';
import { LinkParser } from '../lib/com/hellomonday/utils/LinkParser';
import { ErrorTemplate } from './templates/ErrorTemplate';
import { DefaultTemplate } from './templates/DefaultTemplate';
import { TestModule } from './modules/TestModule/TestModule';
import gsap from 'gsap';
import * as ScrollToPlugin from 'gsap/ScrollToPlugin';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { NewsArticleTemplate } from './templates/NewsArticleTemplate';
import { ShowroomModule } from './modules/ShowroomModule/ShowroomModule';
import { MainMenu } from './ui/MainMenu/MainMenu';
import { CookieBar } from './ui/CookieBar/CookieBar';
import { tracking } from './utils/Tracking';
import { UrlManager } from './utils/URLManager';

const modules = {
	TestModule,
	ArticlesModule,
	ContactModule,
	TeamModule,
	PartnersModule,
	FooterModule,
	ShowroomModule,
	MarkdownModule,
	AffiliatesModule
};
declare global {
	interface Window {
		Main: Main;
	}
}
async function supportsWebp() {
	let supported = false;
	const webpData = 'data:image/webp;base64,UklGRh4AAABXRUJQVlA4TBEAAAAvAAAAAAfQ//73v/+BiOh/AAA=';

	if (self.createImageBitmap) {
		const blob = await fetch(webpData).then(r => r.blob());
		// @ts-ignore
		supported = createImageBitmap(blob).then(() => true, () => false);
	} else {
		return new Promise(res => {
			const webP = new Image();
			webP.src = 'data:image/webp;base64,UklGRjoAAABXRUJQVlA4IC4AAACyAgCdASoCAAIALmk0mk0iIiIiIgBoSygABc6WWgAA/veff/0PP8bA//LwYAAA';
			webP.onload = webP.onerror = () => {
				res(webP.height === 2);
			};
		});
	}
	return supported;
}

class Main {
	private mainMenu: MainMenu;
	private cookieBar: CookieBar;

	constructor() {
		//@ts-ignore
		gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);
		this.quickCheckWebpSupport();
		if (Globals.browserDetect && Globals.browserDetect.name && Globals.browserDetect.name.indexOf('safari') === -1) {
			document.documentElement.classList.add('not-safari');
		} else {
			document.documentElement.classList.add('is-safari');
		}
		document.documentElement.classList.add('site-loaded');
		if (Globals.IS_TOUCH_DEVICE) {
			document.documentElement.classList.add('is-touch');
		}
		if (document.body.querySelector('.ErrorTemplate')) {
			document.documentElement.classList.add('ErrorPage');
			gsap.set('.MainMenu-CoverLayer', { autoAlpha: 0, immediateRender: true });
			return;
		}

		//Save URL parameters
		UrlManager.saveParamsFromUrl();

		//Make sure we have one call to WindowManager to init it
		WindowManager.getInstance();

		Globals.TEMPLATE_MANAGER = new TemplateManager();
		LinkParser.getInstance(Globals.TEMPLATE_MANAGER);
		LinkParser.getInstance().parseNodeList(document.getElementById('SiteWrapper').querySelectorAll('a:not(.noparse)'));

		//Register all modules:
		ModuleFactory.registerModules(modules);

		// Initiate tracking Session first
		tracking.init();
		this.initSite();
	}

	private quickCheckWebpSupport() {
		if (Globals.browserDetect && Globals.browserDetect.name && Globals.browserDetect.name.indexOf('chrome') === -1) {
			//This could technically be untrue for some old versions of chrome.
			this.checkWebpSupport();
		} else {
			Globals.WEBP_SUPPORTED = true;
			Globals.WEBP_SUPPORT_CHECKED = true;
		}
	}

	private async checkWebpSupport() {
		if (!Globals.WEBP_SUPPORT_CHECKED) {
			const supported = await supportsWebp();
			if (supported) {
				Globals.WEBP_SUPPORTED = true;
			}
			Globals.WEBP_SUPPORT_CHECKED = true;
			Globals.SIGNAL_WEBP_SUPPORT_CHECKED.dispatch();
		}
	}

	private initSite = () => {
		// Add Templates
		Globals.TEMPLATE_MANAGER.addTemplate('DefaultTemplate', DefaultTemplate);
		Globals.TEMPLATE_MANAGER.addTemplate('ErrorTemplate', ErrorTemplate);
		Globals.TEMPLATE_MANAGER.addTemplate('NewsArticleTemplate', NewsArticleTemplate);
		Globals.TEMPLATE_MANAGER.addTemplate('HomeTemplate', HomeTemplate);
		Globals.TEMPLATE_MANAGER.init(Globals.TEMPLATE_LAYER, 'home');

		this.mainMenu = new MainMenu(Globals.TEMPLATE_MANAGER);
		this.cookieBar = new CookieBar(document.querySelector('.CookieBar'));
		WindowManager.signalResize.add(this.resize);
		this.resize();
	};

	public resize = () => {
		Globals.TEMPLATE_MANAGER.resize();
	};
}

window.onload = () => {
	(window as any).Main = new Main();
};
