export class URLManager {
	urlParams: object = {};
	originalUrl: string = '';

	// Parameters to clean up from the URL
	cleanUpParams: Array<string> = ['utm_source', 'utm_id', 'utm_medium', 'utm_campaign', 'utm_term', 'utm_content', 'adset', 'adsetid', 'deep_link', 'referrer', 'affiliate_id'];

	constructor() {}

	// Save URL paramenters
	public saveParamsFromUrl() {
		let search = window.location.search.substring(1);

		if (search.length) {
			let params = Object.fromEntries(new URLSearchParams(search));
			this.urlParams = params;
		}

		this.cleanParamsFromUrl();

		return this.urlParams;
	}

	// Clean up the desired parameters from the URL (eg: for safe copy/paste of URL)
	public cleanParamsFromUrl() {
		let location = window.location;

		let parsed = new URL(location.toString());
		this.cleanUpParams.forEach(e => parsed.searchParams.delete(e));

		if (history.replaceState) {
			this.originalUrl = location.toString();
			history.replaceState({}, '', parsed.toString());
		}

		return parsed.toString();
	}
}

export const UrlManager = new URLManager();
