import { AbstractModule } from '../../../lib/com/hellomonday/templateManager/AbstractModule';
import { AbstractTemplate } from '../../templates/AbstractTemplate';
import { Globals } from '../../utils/Globals';
import gsap from 'gsap';
import { tracking } from '../../utils/Tracking';

interface ArticleType {
	el: HTMLElement;
	slug: string;
	category: string;
	tag: string;
	date: string;
	tagName: string;
}

export class ArticlesModule extends AbstractModule {
	private DOMArticles_: NodeListOf<HTMLDivElement>;
	private DOMArticlesWrapper_: HTMLDivElement;
	private articles_: ArticleType[];
	private articlesData_: any;
	private currentCategory: string;
	private DOMCategoriesWrapper_: HTMLElement;
	private DOMCategoryTemplate: HTMLElement;
	private DOMSubCategoriesWrapper_: Element;
	private DOMSubCategoryTemplate_: Element;
	private DOMFilterButton_: HTMLButtonElement;
	private currentArticles_: any[];
	private currentFiltering: number;
	private DOMPressKitSectionHeader: NodeListOf<Element>;
	private DOMActiveCategory_: Element;
	private DOMMobileSubCategory: HTMLSelectElement;
	private DOMPressKitSectionBody: NodeListOf<Element>;
	private DOMPressKitSectionBodyInner: NodeListOf<Element>;
	private DOMPressKitSectionFiles: NodeListOf<Element>;
	private DOMPressKitSectionFilesInner: NodeListOf<Element>;

	constructor(element: HTMLElement, template: AbstractTemplate) {
		super(element, template);

		this.DOMArticlesWrapper_ = element.querySelector('.articles');
		this.DOMArticles_ = element.querySelectorAll('.ArticlePreview');

		this.DOMCategoriesWrapper_ = element.querySelector('.NewsMainCategories');
		this.DOMSubCategoriesWrapper_ = element.querySelector('.NewsSubCategories');
		if (this.DOMSubCategoriesWrapper_) {
			this.DOMSubCategoryTemplate_ = this.DOMSubCategoriesWrapper_.querySelector('.ArticleCategory');
		}
		this.DOMFilterButton_ = element.querySelector('.ArticlesFilterTrigger');
		this.DOMActiveCategory_ = element.querySelector('.MainCategoryTitle.active');

		this.DOMMobileSubCategory = element.querySelector('.MobileSubCategories');

		this.articles_ = [];
		this.articlesData_ = [];
		this.currentCategory = element.dataset.currentCategory;
		this.currentArticles_ = [];

		this.currentFiltering = -1;

		if (this.DOMArticles_.length > 0) {
			this.parseArticles();
			this.bindEvents_();
			this.onFilter_();
			this.filterByDate_();
		}

		if (this.DOMMobileSubCategory) {
			const hash = location.hash.replace('#', '');
			this.DOMMobileSubCategory.value = hash;
		}

		this.DOMPressKitSectionHeader = element.querySelectorAll('.PressKitSectionHeader');
		this.DOMPressKitSectionBody = element.querySelectorAll('.PressKitSectionBody');
		this.DOMPressKitSectionBodyInner = element.querySelectorAll('.SectionBodyInner');

		this.DOMPressKitSectionFiles = element.querySelectorAll('.FilesSectionHeader');
		this.DOMPressKitSectionFilesInner = element.querySelectorAll('.FilesSectionBody');

		if (this.DOMPressKitSectionHeader.length > 0) {
			this.DOMPressKitSectionHeader.forEach((el, index) => {
				el.addEventListener('click', () => {
					el.classList.toggle('toggled');
					const h = this.DOMPressKitSectionBodyInner[index].getBoundingClientRect().height;
					if (el.classList.contains('toggled')) {
						gsap.to(this.DOMPressKitSectionBody[index], { height: 'auto', duration: 0.8, ease: 'power3.inOut' });
					} else {
						gsap.to(this.DOMPressKitSectionBody[index], { height: 0, duration: 0.8, ease: 'power3.inOut' });
					}
				});

				if (el.classList.contains('toggled')) {
					gsap.set(this.DOMPressKitSectionBody[index], { height: 'auto' });
				}
			});
		}

		if (this.DOMPressKitSectionFiles.length > 0) {
			this.DOMPressKitSectionFiles.forEach((el, index) => {
				el.addEventListener('click', () => {
					el.classList.toggle('toggled');
					if (el.classList.contains('toggled')) {
						gsap.to(this.DOMPressKitSectionFilesInner[index], { height: 'auto', duration: 0.8, ease: 'power3.inOut' });
					} else {
						gsap.to(this.DOMPressKitSectionFilesInner[index], { height: 0, duration: 0.8, ease: 'power3.inOut' });
					}
				});
			});
		}

		if (Globals.BODY.classList.contains('dark-mode')) {
			Globals.BODY.classList.remove('dark-mode');
		}

		// Categories click tracking
		element.querySelectorAll('.MainCategoryTitle').forEach(cat => {
			cat.addEventListener('click', e => {
				const target = (e.target as HTMLElement).closest('a');
				let clickAction = 'go_to_page';

				if (target.classList.contains('ExternalCategory')) {
					clickAction = 'open_external';
				}

				tracking.trackClick('newsroom_menu_item', clickAction, target.getAttribute('href'));
			});
		});

		// SubCategories click tracking
		element.querySelectorAll('.ArticleSubCategory').forEach(cat => {
			cat.addEventListener('click', e => {
				const target = (e.target as HTMLElement).closest('a');
				tracking.trackClick('newsroom_menu_item', 'go_to_page', target.getAttribute('href'));
			});
		});

		// Articles click tracking
		element.querySelectorAll('.ArticlePreview').forEach(article => {
			article.addEventListener('click', e => {
				const target = (e.target as HTMLElement).closest('a');
				tracking.trackClick('newsroom_item', 'open_item', target.getAttribute('href'));
			});
		});

		// Articles click tracking
		element.querySelectorAll('.PdfFile').forEach(pdf => {
			pdf.addEventListener('click', e => {
				const target = (e.target as HTMLElement).closest('a');

				let title = target
					.querySelector('.title')
					.innerHTML.split('•')[0]
					.toLowerCase()
					.replaceAll(' ', '_');

				// For child elements
				if (target.dataset.title) {
					title = target.dataset.title
						.split('•')[0]
						// If wanting to track language version in element or action.
						// It's already sent in the destinationUrl
						// .concat(title, ' ')
						.toLowerCase()
						.replaceAll(' ', '_');
				}

				console.log('title', title);
				tracking.trackClick(title + 'link', title + 'open', target.getAttribute('href'));
			});
		});
	}

	private bindEvents_() {
		this.DOMFilterButton_.addEventListener('click', this.filterByDate_);
		window.addEventListener('hashchange', this.onFilter_);

		if (this.DOMActiveCategory_) {
			this.DOMActiveCategory_.addEventListener('click', this.resetFilter_);
		}

		if (this.DOMMobileSubCategory) {
			this.DOMMobileSubCategory.addEventListener('change', this.mobileFilter_);
		}
	}

	private mobileFilter_ = e => {
		if (e.currentTarget.value === '') {
			this.resetFilter_(e);
			location.hash = '';
		} else {
			location.hash = '#' + e.currentTarget.value;
		}
	};

	private resetFilter_ = e => {
		this.currentArticles_ = this.articles_;
		this.DOMArticlesWrapper_.innerHTML = '';
		this.currentArticles_.forEach(item => {
			this.DOMArticlesWrapper_.appendChild(item.el);
		});
	};

	private onFilter_ = () => {
		const hash = location.hash.replace('#', '');

		if (hash) {
			const activeSubcategory = this.element.querySelector(`.ArticleSubCategory.active`);
			const nextSubcategory = this.element.querySelector(`.ArticleSubCategory[data-category="${hash}"]`);

			if (nextSubcategory) {
				nextSubcategory.classList.add('active');
			}

			if (activeSubcategory) {
				activeSubcategory.classList.remove('active');
			}

			const filteredArticles = this.articles_.filter(article => article.tag === hash);

			this.DOMArticlesWrapper_.innerHTML = '';
			filteredArticles.forEach(item => {
				this.DOMArticlesWrapper_.appendChild(item.el);
			});

			this.currentArticles_ = filteredArticles;
		} else {
			this.currentArticles_ = this.articles_;
		}
	};

	private filterByDate_ = () => {
		this.currentFiltering *= -1;
		this.DOMFilterButton_.querySelector('.filter-text').innerHTML = this.currentFiltering === 1 ? 'Newest first' : 'Oldest first';
		this.DOMFilterButton_.classList.toggle('up');

		const newArray = this.currentArticles_.sort((a, b) => {
			if (a.date < b.date) {
				return this.currentFiltering * 1;
			}
			if (a.date > b.date) {
				return this.currentFiltering * -1;
			}
			return 0;
		});

		this.DOMArticlesWrapper_.innerHTML = '';
		newArray.forEach(item => {
			this.DOMArticlesWrapper_.appendChild(item.el);
		});
	};

	private findObject_ = (array, category) => {
		return array.findIndex(data => data.id === category);
	};

	private parseArticles() {
		this.DOMArticles_.forEach(article => {
			const { category, tag, date, slug, tagName } = article.dataset;

			this.articles_.push({
				el: article,
				category,
				tag,
				date,
				slug,
				tagName
			});

			const categoryIndex = this.findObject_(this.articlesData_, category);

			if (categoryIndex < 0) {
				this.articlesData_.push({
					id: category,
					amount: 1,
					tags: [
						{
							id: tag,
							amount: 1,
							name: tagName
						}
					]
				});
			} else {
				this.articlesData_[categoryIndex].amount += 1;

				const tagIndex = this.findObject_(this.articlesData_[categoryIndex].tags, tag);
				if (tagIndex < 0) {
					this.articlesData_[categoryIndex].tags.push({
						id: tag,
						amount: 1,
						name: tagName
					});
				} else {
					this.articlesData_[categoryIndex].tags[tagIndex].amount += 1;
				}
			}
		});

		this.articlesData_.forEach(data => {
			const el = this.element.querySelector('.NewsCategory[data-category="' + data.id + '"]');

			if (el) {
				el.querySelector('.CategoryAmount').innerText = data.amount;
			}
		});

		this.currentArticles_ = this.articles_;
		this.createFilters_();
	}

	private createFilters_ = () => {
		if (!this.DOMSubCategoriesWrapper_) return;

		this.DOMSubCategoriesWrapper_.innerHTML = '';
		const currentCategoryData = this.articlesData_.find(data => data.id === this.currentCategory);
		currentCategoryData.tags.sort((a, b) => {
			if (a.name < b.name) {
				return -1;
			}
			if (a.name > b.name) {
				return 1;
			}
		});

		currentCategoryData.tags.forEach(data => {
			const cloneNode = this.DOMSubCategoryTemplate_.cloneNode(true) as HTMLElement;
			const DOMCategoryName = cloneNode.querySelector('.SubCategoryName');
			const DOMCategoryAmount = cloneNode.querySelector('.SubCategoryAmount');
			const DOMCategoryHash = cloneNode.querySelector('.SubCategoryHash') as HTMLLinkElement;

			DOMCategoryName.innerHTML = data.name;
			DOMCategoryAmount.innerHTML = data.amount;
			DOMCategoryHash.href = `#${data.id}`;
			cloneNode.dataset.category = data.id;
			//
			this.DOMSubCategoriesWrapper_.appendChild(cloneNode);

			if (this.DOMMobileSubCategory) {
				const option = document.createElement('option');
				option.innerText = data.name;
				option.value = data.id;
				this.DOMMobileSubCategory.appendChild(option);
			}
		});
	};

	private filterArticle_ = (category, tag) => {
		const articlesInCategory = this.articles_.filter(article => article.category === category);
		articlesInCategory.forEach(article => {
			this.DOMArticlesWrapper_.appendChild(article.el);
		});
	};

	kill() {
		super.kill();
	}
}
