import gsap from 'gsap';
import { AbstractModule } from '../../../lib/com/hellomonday/templateManager/AbstractModule';
import { Globals } from '../../utils/Globals';
import { tracking } from '../../utils/Tracking';

export class PartnersModule extends AbstractModule {
	private DOMPageHeader: any;
	private DOMPartners: NodeListOf<Element>;

	constructor(element, module) {
		super(element, module);

		this.element = element;
		this.DOMPageHeader = this.element.querySelector('.PageHeader');

		Globals.BODY.classList.add('dark-mode');
		document.documentElement.classList.add('noNoise');
		this.resize();

		// Partners click tracking
		this.DOMPartners = element.querySelectorAll('.Partner');
		this.DOMPartners.forEach(partner => {
			partner.addEventListener('click', e => {
				const target = e.target as HTMLElement;
				tracking.trackClick('partners_item', 'go_to_page', target.getAttribute('href'));
			});
		});
	}

	kill() {
		super.kill();
		Globals.BODY.classList.remove('dark-mode');
		document.documentElement.classList.remove('noNoise');
	}

	resize() {
		super.resize();
		const y = (window.innerWidth * 1362) / 1680;
		this.element.style.backgroundPositionY = gsap.utils.clamp(-9999, 0, 1362 - y) + 'px';
	}
}
