import { getCookie, setCookie } from '../../lib/com/hellomonday/utils/CookieUtils';
import { Globals } from './Globals';
import { UrlManager } from './URLManager';

export class Tracking {
	cookieId: string = null;
	sentUrlParams: boolean = false;

	pageViewParams: object = {
		utm_source: 'source',
		utm_medium: 'medium',
		utm_campaign: 'referral_campaign',
		utm_term: 'term',
		utm_content: 'content',
		adset: 'adset',
		adsetid: 'adsetid'
		// utm_id: 'adsetid,'
	};

	initParams: object = {
		utm_source: 'channel',
		utm_medium: 'medium',
		utm_campaign: 'campaign',
		utm_term: 'term',
		utm_content: 'content',
		adset: 'adset',
		deep_link: 'deep_link',
		referrer: 'referral',
		affiliate_id: 'affiliate_id'
	};

	public init() {
		// Load Cookie before init
		this.checkCookie();

		this.trackEvent('session');
	}

	private checkCookie() {
		if (getCookie('tracker') !== null) {
			this.cookieId = getCookie('tracker');
		}
	}

	public initFirstVisitCookie() {
		if (getCookie('tracker') === null) {
			this.cookieId = Math.random()
				.toString(36)
				.substr(2, 15);
			setCookie('tracker', this.cookieId, 30);

			let initData = {
				description: 'Star Atlas Initializer'
			};

			Object.assign(initData, this.getUrlParameters(this.initParams));

			this.trackEvent('init', initData);
		}
	}

	public trackPageView() {
		let pageViewData = {
			event_type: 'page_load',
			description: window.location.href
		};

		// Do not send parameters twice, when changing View
		if (!this.sentUrlParams) {
			Object.assign(pageViewData, this.getUrlParameters(this.pageViewParams));
			this.sentUrlParams = true;
		}

		this.trackEvent('adreferral', pageViewData);
	}

	public trackClick(el: string = '', action: string = '', destination_url: string = '') {
		const templatePrevPath = Globals.TEMPLATE_MANAGER.getPreviousPath();
		const domain = window.location.protocol + '//' + window.location.host;
		const prevPath = templatePrevPath !== 'initialLoad' ? domain + '/' + templatePrevPath : domain;
		let destinationUrl = destination_url === '' ? null : domain + destination_url;

		if (typeof destination_url === 'string') {
			// Anchor urls
			if (destination_url.indexOf('#') === 0) {
				destinationUrl = destination_url;
			}

			// External urls
			if (destination_url.indexOf('http') > -1) {
				destinationUrl = destination_url;
			}
		}

		this.trackEvent('click', {
			click_ts: new Date().toISOString(),
			cookie_id: this.cookieId,
			element: el,
			action: action,
			url: window.location.href, //prevPath,
			destination_url: destinationUrl
		});

		window.fb('track', 'click', {
			element: el,
			action: action,
			url: prevPath,
			destination_url: destinationUrl
		});
	}

	public trackEngagement(action: string = '', app_id: string = '', char_value_1: string = '') {
		this.trackEvent('engagement', {
			event_ts: new Date().toISOString(),
			action_type: action,
			app_id,
			char_value_1
		});

		window.fb('track', 'engagement', {
			action_type: action
		});
	}

	public trackSocial(action: string = '', platform: string = '') {
		this.trackEvent('social', {
			event_ts: new Date().toISOString(),
			action_type: action,
			platform: platform
		});
	}

	private trackEvent(eventType: string = '', obj: any = {}) {
		// Set timestamp
		if ('event_ts' in obj === false) {
			obj.event_ts = new Date().toISOString();
		}

		// Set cookie id
		if ('cookie_id' in obj === false && this.cookieId) {
			obj.cookie_id = this.cookieId;
		}

		// console.log('sending to Signalfox. eventType', eventType, 'object', obj);

		if (window.SFLoad) {
			console.log('sent to Signalfox. eventType', eventType, 'object', obj);
			window.sf.logEvent(eventType, obj);
		}
	}

	public trackFBConversion(productId: string = '', productName: string = '') {
		window.fb('track', 'Purchase', {
			content_ids: [productId]
		});

		window.fb('track', 'engagement', {
			action_type: 'buy_item_button',
			app_id: productId,
			char_value_1: productName
		});
	}

	public getUrlParameters(paramsMap: any = {}) {
		let urlParams = UrlManager.urlParams,
			obj: any = {};

		for (const [utm_key, sf_key] of Object.entries(paramsMap)) {
			if (utm_key in urlParams) {
				obj.sf_key = urlParams[utm_key];
			}
		}

		return obj;
	}
}
export const tracking = new Tracking();
