import { AbstractTemplate } from './AbstractTemplate';
import {tracking} from "../utils/Tracking";

export class HomeTemplate extends AbstractTemplate {

	private mainScene: any;
	private videoPlayer: any;

	constructor(element, template) {
		super(element, template);
		document.documentElement.classList.add('HomeTemplatePage');
		import(/* webpackChunkName: "MainScene", webpackPrefetch: true */ '../modules/HomeModule/ui/MainScene').then((result) => {
			this.mainScene = new result.MainScene(element.querySelector('#ViewContainer'));
		});
		import(/* webpackChunkName: "VideoJSPlayer", webpackPrefetch: true */ '../ui/VideoJSPlayer').then((result) => {
			this.videoPlayer = new result.VideoJSPlayer(element);
		});

		// Landing Menu click tracking
		element.querySelectorAll('.LandingMenu a').forEach((link) => {
			link.addEventListener('click', (e) => {
				const target = (e.target as HTMLElement);
				tracking.trackClick('menu_item_bottom_left',  'go_to_page', target.getAttribute('href'));
			});
		});
	}

	templateOut(newPath, speed) {
		super.templateOut(newPath, speed);
		document.documentElement.classList.remove('darkMenu');
	}


	templateIn() {
		super.templateIn();
	}

	kill() {
		super.kill();
		this.mainScene.kill();
		if (this.videoPlayer) {
			this.videoPlayer.kill();
		}
	}

	templateOutComplete() {
		super.templateOutComplete();
		document.documentElement.classList.remove('HomeTemplatePage');
	}

	resize() {
		super.resize();
		if (this.videoPlayer) {
			this.videoPlayer.resize();
		}
	}

}
