import gsap from 'gsap';
import { AbstractModule } from '../../../lib/com/hellomonday/templateManager/AbstractModule';
import { Globals } from '../../utils/Globals';
import { tracking } from '../../utils/Tracking';

export class ContactModule extends AbstractModule {
	private DOMFormControls: any;
	private DOMConditionLink: any;
	private DOMSubmitButton: any;
	private ISFormValid: boolean;
	private DATAControls: any[];
	private DOMIntroText: any;
	private DOMValidationText: any;
	private DOMForm_: HTMLFormElement;
	private domErrorMessage: HTMLElement;

	constructor(element, module) {
		super(element, module);

		this.DOMFormControls = element.querySelectorAll('.FormControl');
		this.DOMConditionLink = element.querySelector('.CheckboxControl a');
		this.DOMSubmitButton = element.querySelector('.SubmitButton');
		this.DOMIntroText = element.querySelector('.IntroText');
		this.DOMValidationText = element.querySelector('.ValidationText');
		this.DOMForm_ = element.querySelector('.Form');
		this.domErrorMessage = element.querySelector('.errorMessage');
		this.ISFormValid = false;
		this.DATAControls = [];

		this.DOMFormControls.forEach(el => {
			const input = el.querySelector('input, textarea, select');
			this.DATAControls.push({
				el,
				input,
				name: input.name,
				isValid: true
			});
		});

		if (Globals.BODY.classList.contains('dark-mode')) {
			Globals.BODY.classList.remove('dark-mode');
		}
		this.bindEvents_();
	}

	private bindEvents_ = () => {
		this.DOMConditionLink.addEventListener('click', this.onLinkClick_);
		this.DOMForm_.addEventListener('submit', this.onSubmit_);

		this.DATAControls.forEach(control => {
			control.input.addEventListener('blur', e => {
				if (!control.input.validity.valid) {
					control.el.classList.add('error');
				} else {
					control.el.classList.remove('error');
				}
			});

			control.input.addEventListener('keydown', e => {
				if (control.input.validity.valid) {
					control.el.classList.remove('error');
				}
			});
			control.input.addEventListener('change', e => {
				if (!control.input.validity.valid) {
					control.el.classList.add('error');
				} else {
					control.el.classList.remove('error');
				}
			});
		});
	};

	private onLinkClick_ = e => {
		e.stopPropagation();
	};

	private onSubmit_ = e => {
		e.preventDefault();

		const isValid = this.validate_();
		if (isValid) {
			this.hideError();
			this.element.classList.add('submitting');
			var object = {};
			const formData = new FormData(this.DOMForm_);

			formData.forEach((value, key) => {
				// Reflect.has in favor of: object.hasOwnProperty(key)
				if (!Reflect.has(object, key)) {
					object[key] = value;
					return;
				}
				if (!Array.isArray(object[key])) {
					object[key] = [object[key]];
				}
				object[key].push(value);
			});
			var json = JSON.stringify(object);

			fetch('https://account.staratlas.com/contact', {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					Accept: 'application/json'
				},
				body: json
			})
				.then(response => {
					if (response.status == 200) {
						this.onValidateAnimation_();
					} else if (response.status == 409) {
						this.showError('409');
					} else if (response.status == 429) {
						this.showError('429');
					} else {
						this.showError('general');
					}
				})
				.catch(error => {
					this.showError('general');
				});
		}
	};
	private hideError = () => {
		this.element.classList.remove('error');
		this.element.classList.remove('submitting');
		if (this.element.querySelector('.active')) {
			this.element.querySelector('.active').classList.remove('active');
		}
	};

	private showError = (className: string) => {
		this.hideError();
		this.element.classList.remove('success');
		this.element.classList.add('error');
		this.element.querySelector('.error-' + className).classList.add('active');
	};

	private onValidateAnimation_ = () => {
		const y = 0;
		this.hideError();
		this.element.classList.add('submitted');
		gsap.set(this.DOMValidationText.children, { y: y });
		gsap.to(this.DOMIntroText.children, {
			opacity: 0,
			y: -y,
			ease: 'power3.inOut',
			duration: 0.5,
			stagger: 0.05
		});
		gsap.to(this.DOMValidationText.children, {
			opacity: 1,
			y: 0,
			ease: 'power3.inOut',
			duration: 0.5,
			delay: 0.7,
			stagger: 0.05
		});

		tracking.trackClick('contact_submit_button', 'submit_form');
		tracking.trackEngagement('Contact us');
	};

	private validate_ = () => {
		let isValid = true;

		this.DATAControls.forEach(control => {
			if (!control.input.validity.valid) {
				control.el.classList.add('error');
				isValid = false;
			} else {
				control.el.classList.remove('error');
			}
		});

		return isValid;
	};

	kill() {
		super.kill();
	}

	resize() {
		super.resize();
	}
}
