import gsap from 'gsap';
import { AbstractModule } from '../../../lib/com/hellomonday/templateManager/AbstractModule';
import { Globals } from '../../utils/Globals';
import { tracking } from '../../utils/Tracking';

export class TeamModule extends AbstractModule {
	private DOMLinkToOpenPositions: any;
	private DOMProfiles: NodeListOf<Element>;

	constructor(element, module) {
		super(element, module);

		if (Globals.BODY.classList.contains('dark-mode')) {
			Globals.BODY.classList.remove('dark-mode');
		}

		this.DOMLinkToOpenPositions = element.querySelector('.LinkToOpenPositions');
		this.DOMLinkToOpenPositions.addEventListener('click', this.goToOpenPositions_);

		if (location.hash === '#open-positions') {
			this.scrollTo_(this.DOMLinkToOpenPositions);
		}

		// Profiles click tracking
		this.DOMProfiles = element.querySelectorAll('.TeamPerDepartment .ListItem');
		this.DOMProfiles.forEach(link => {
			link.addEventListener('click', e => {
				const target = (e.target as HTMLElement).closest('a');

				if (target) tracking.trackClick('team_profile_link', 'go_to_page', target.getAttribute('href'));
			});
		});
	}

	goToOpenPositions_ = e => {
		this.scrollTo_(e.currentTarget);
	};

	scrollTo_(link) {
		if (location.pathname.replace(/^\//, '') == link.pathname.replace(/^\//, '') && location.hostname == link.hostname) {
			gsap.to(window, { duration: 0.8, delay: 0.0, scrollTo: { y: link.hash, offsetY: 0 }, ease: 'power3.inOut' });
		}

		tracking.trackClick('team_open_positions_link', 'go_to_section', link.hash);
		tracking.trackEngagement('see_open_positions');
	}

	kill() {
		super.kill();
	}

	resize() {
		super.resize();
	}
}
