import { AbstractTemplate } from '../../templates/AbstractTemplate';
import { AbstractModule } from '../../../lib/com/hellomonday/templateManager/AbstractModule';
import { gsap } from 'gsap';
import { tracking } from '../../utils/Tracking';

export class FooterModule extends AbstractModule {
	private socialLinks = document.querySelectorAll('.FooterSocials .SocialsList a');
	private highlightedLinks = document.querySelectorAll('.HighlightedLinks a');
	private footerNavLinks = document.querySelectorAll('.FooterNav a');

	constructor(element: HTMLElement, template: AbstractTemplate) {
		super(element, template);

		// Footer Menu click tracking
		this.footerNavLinks.forEach(cta => {
			cta.addEventListener('click', e => {
				const target = (e.target as HTMLElement).closest('a');
				tracking.trackClick('footer_nav_link', 'go_to_page', target.getAttribute('href'));
			});
		});

		// Footer Menu Highlighted Links click tracking
		this.highlightedLinks.forEach(cta => {
			cta.addEventListener('click', e => {
				const target = (e.target as HTMLElement).closest('a');
				tracking.trackClick('footer_highlighted_link', 'go_to_page', target.getAttribute('href'));
			});
		});

		// Social Links click tracking
		this.socialLinks.forEach(cta => {
			cta.addEventListener('click', e => {
				const target = (e.target as HTMLElement).closest('a');
				tracking.trackSocial('footer_social_link', target.dataset.social);
			});
		});
	}

	kill() {
		super.kill();
	}
}
