import { gsap } from 'gsap';
import { tracking } from '../../utils/Tracking';
import { getCookie, setCookie } from '../../../lib/com/hellomonday/utils/CookieUtils';

export class CookieBar {
	private element: HTMLElement;
	private btnAllowCookies: HTMLElement;
	private btnClose: HTMLElement;

	constructor(element) {
		this.element = element;
		this.btnAllowCookies = this.element.querySelector('.allowCookies');
		this.btnClose = this.element.querySelector('.CookieBar-close');

		this.btnAllowCookies.addEventListener('click', this.allowCookies.bind(this));
		this.btnClose.addEventListener('click', this.close.bind(this));

		if (getCookie('tracker') === null && getCookie('cookieAccept') === null) {
			this.showCookieBar();
		}
	}

	private allowCookies = e => {
		this.closeCookieBar();
		tracking.initFirstVisitCookie();
		e.preventDefault();
	};

	private close = e => {
		if (getCookie('cookieAccept') === null) {
			setCookie('cookieAccept', 'false', 30);
		}
		this.closeCookieBar();
		e.preventDefault();
	};

	private showCookieBar = () => {
		this.element.style.display = 'block';
		gsap.to(this.element, { opacity: 1, duration: 0.3 });
	};

	private closeCookieBar = () => {
		gsap.to(this.element, {
			y: 20,
			opacity: 0,
			duration: 0.3,
			onComplete: () => {
				this.element.style.display = 'none';
			}
		});
	};
}
