import gsap from 'gsap';
import { AbstractModule } from '../../../lib/com/hellomonday/templateManager/AbstractModule';
import { Globals } from '../../utils/Globals';
import { tracking } from '../../utils/Tracking';

export class AffiliatesModule extends AbstractModule {
	private DOMPageHeader: any;
	private DOMCtas: NodeListOf<Element>;

	constructor(element, module) {
		super(element, module);

		this.element = element;
		this.DOMPageHeader = this.element.querySelector('.PageHeader');
		this.DOMCtas = this.element.querySelectorAll('.AffiliatePreviewContent a');
		this.DOMCtas.forEach(cta => {
			cta.addEventListener('click', this.onAffiliateClick.bind(this, cta));
		});

		document.documentElement.classList.add('AffiliatesModuleInPage');
		this.resize();
	}

	onAffiliateClick(cta) {
		tracking.trackClick('affiliate_promo_button', 'go_to_page', cta.getAttribute('href'));
	}

	kill() {
		super.kill();
		document.documentElement.classList.remove('AffiliatesModuleInPage');
		this.DOMCtas.forEach(cta => {
			cta.removeEventListener('click', this.onAffiliateClick.bind(this, cta));
		});
	}

	resize() {
		super.resize();
		const y = (window.innerWidth * 1362) / 1680;
		this.element.style.backgroundPositionY = gsap.utils.clamp(-9999, 0, 1362 - y) + 'px';
	}
}
