import { Globals } from '../utils/Globals';
import { Resizer } from '../utils/Resizer';
import {AbstractTemplate} from './AbstractTemplate';
import gsap from "gsap"


export class NewsArticleTemplate extends AbstractTemplate {
	private DOMCodeBlocks_: any;
	private DOMImagesPopIn_: any;
	private DOMPopInLayer_: any;
	private DOMPopInLayerImage_: any;
	private DOMPopInLayerCaption_: any;
	private DOMPopInLayerImageInner_: any;
	private DOMPopInLayerImageContainer_: any;
	private currentImageSize_: { width: number; height: number };
	private ISOpened_: boolean;
	private RECTPopIn_: any;
	private DOMCloseButton_: any;
	private DOMLayer_: any;
	private DOMBackToTop_: HTMLElement;

	constructor(element, template) {
		super(element, template);

		this.DOMCodeBlocks_ = element.querySelectorAll(".ArticleBlockCodeEmbed .code");

		import(/* webpackChunkName: "hljs", webpackPrefetch: true */ 'highlight.js').then(({ default: hljs }) => {
			console.log(hljs);
			this.DOMCodeBlocks_.forEach((el) => {
				const html = hljs.highlightAuto(el.dataset.code).value
				el.querySelector("code").innerHTML = html;
			})
		});

		this.DOMImagesPopIn_ = element.querySelectorAll(".PopInEnable")
		this.DOMPopInLayer_ = element.querySelector(".ImagePopIn")
		this.DOMPopInLayerImage_ = this.DOMPopInLayer_.querySelector("img")
		this.DOMPopInLayerImageInner_ = this.DOMPopInLayer_.querySelector(".imageInner")
		this.DOMPopInLayerImageContainer_ = this.DOMPopInLayer_.querySelector(".PopInContent")
		this.DOMPopInLayerCaption_ = this.DOMPopInLayer_.querySelector("p")
		this.DOMCloseButton_ = element.querySelector(".closeLayerButton")
		this.DOMLayer_ = element.querySelector(".Layer");
		this.DOMBackToTop_ = element.querySelector(".BackToTop");
		this.ISOpened_ = false

		if(Globals.BODY.classList.contains("dark-mode")) {
			Globals.BODY.classList.remove("dark-mode");
		}

		this.bindEvents_()
	}

	private bindEvents_() {
		this.DOMImagesPopIn_.forEach((imageContainer) => {
			imageContainer.addEventListener("click", this.openPopIn_)
		})
		this.DOMCloseButton_.addEventListener("click", this.closePopIn_)
		this.DOMLayer_.addEventListener("click", this.closePopIn_)
		this.DOMBackToTop_.addEventListener("click", this.scrollToTop_)
	}

	private onKeyDown_ = (e) => {
		if(e.code === "Escape") {
			this.closePopIn_()
			window.removeEventListener("keydown", this.onKeyDown_)
		}
	}

	private closePopIn_ = () => {
		if(!this.ISOpened_) return

		this.ISOpened_ = false;
		gsap.to(this.DOMPopInLayer_, {autoAlpha: 0, duration: 0.4});
	}

	private scrollToTop_ = () => {
		gsap.to(window, {duration: .8, scrollTo: 0, ease: 'power2.out',});
	}

	private openPopIn_ = (e) => {
		const el = e.currentTarget;
		const {imageUrl, imageCaption, imageWidth, imageHeight} = el.dataset;
		this.currentImageSize_ = {width: parseInt(imageWidth), height: parseInt(imageHeight)}

		if(imageUrl) {
			this.DOMPopInLayerImageInner_.style.paddingBottom = parseInt(imageHeight) / parseInt(imageWidth) * 100 + "%"
			this.DOMPopInLayerImage_.src = imageUrl;
			this.DOMPopInLayerCaption_.innerText = imageCaption;
			this.ISOpened_ = true;
			this.resize()
			gsap.to(this.DOMPopInLayer_, {autoAlpha: 1, duration: 0.4});
			window.addEventListener("keydown", this.onKeyDown_)
		}
	}

	public resize() {
		super.resize();
		this.RECTPopIn_ = this.DOMPopInLayer_.getBoundingClientRect()
		if(this.ISOpened_) {
			const sizes = Resizer.getFitInside(this.currentImageSize_.width, this.currentImageSize_.height, Math.min(this.RECTPopIn_.width, 1360), window.innerHeight * 0.75)
			this.DOMPopInLayerImageContainer_.style.width = sizes.width + "px";
		}
	}

	public templateOut(newPath, speed) {
		super.templateOut(newPath, speed);

	}

}
