import gsap from 'gsap';
import { Globals } from '../../../utils/Globals';
import { tracking } from '../../../utils/Tracking';

export class ShowroomPopIn {
	private element_: HTMLDivElement;
	private PopInDOMStructure_: { el: HTMLDivElement; description: Element; media: Element; title: Element; ctas: Element; infos: Element; bgOverlay: HTMLDivElement; bg: HTMLDivElement };
	private DOMClosePopIn_: HTMLDivElement;
	private DOMMediaWrapper_: HTMLDivElement;
	private DOMImageEmbed_: HTMLDivElement;
	private DOMSketchFabEmbed_: HTMLDivElement;
	private currentMediaType_: string;
	private currentImageSize_: { width: any; height: any };
	private currentMediaID: number;
	private open = false;
	private openDuration = 1.5;
	private delayedCallToSourceSet: gsap.core.Tween;

	constructor(element: HTMLDivElement) {
		this.element_ = element;

		this.PopInDOMStructure_ = {
			el: element,
			title: element.querySelector('.ItemTitle'),
			description: element.querySelector('.ItemDescription'),
			infos: element.querySelector('.ItemInfos'),
			ctas: element.querySelector('.ItemCtas'),
			media: element.querySelector('.ItemMedia'),
			bgOverlay: element.parentElement.querySelector('.ShowroomPopIn-Background-overlay'),
			bg: element.parentElement.querySelector('.ShowroomPopIn-Background')
		};
		this.DOMClosePopIn_ = element.querySelector('.closeLayerButton');
		this.DOMMediaWrapper_ = element.querySelector('.MediaWrapper');
		this.DOMImageEmbed_ = element.querySelector('.ImageEmbed');
		this.DOMSketchFabEmbed_ = element.querySelector('.SketchFabEmbed');

		// gsap.set(this.PopInDOMStructure_.bg, {force3D: true, opacity: 0});
		gsap.set(this.PopInDOMStructure_.bgOverlay, { scale: 10, force3D: true, opacity: 0, yPercent: 500 });
		this.bindEvents_();
	}

	private bindEvents_ = () => {
		this.DOMClosePopIn_.addEventListener('click', this.closePopIn_);
	};

	private onKeyDown_ = e => {
		if (e.code === 'Escape') {
			this.closePopIn_();
		}
	};

	private closePopIn_ = () => {
		// Globals.BODY.style.overflow = 'auto';
		if (this.open) {
			if (this.delayedCallToSourceSet) {
				this.delayedCallToSourceSet.pause();
			}
			window.removeEventListener('keydown', this.onKeyDown_);
			gsap.killTweensOf(this.PopInDOMStructure_.bgOverlay);
			gsap.killTweensOf(this.element_);

			// gsap.to(this.PopInDOMStructure_.bg, {force3D: true, opacity: 0, duration: this.openDuration * 0.1, delay: this.openDuration * 0.5});
			gsap.to(this.PopInDOMStructure_.bgOverlay, { force3D: true, opacity: 0, duration: this.openDuration * 0.25, delay: this.openDuration * 0.65 });
			// gsap.to(this.PopInDOMStructure_.bg, {scale: 10, force3D: true, duration: this.openDuration});

			gsap.to(this.PopInDOMStructure_.bgOverlay, { yPercent: 500, ease: 'power4.inOut', force3D: true, duration: this.openDuration });
			gsap.to(this.PopInDOMStructure_.bgOverlay, {
				scale: 10,
				ease: 'power4.out',
				force3D: true,
				duration: this.openDuration,
				onComplete: () => {
					this.DOMMediaWrapper_.classList.remove('visible');
				}
			});
			gsap.set(this.element_, {
				autoAlpha: 0,
				delay: this.openDuration * 0.5
			});

			this.open = false;
		}
	};

	public openPopIn = (item, index) => {
		if (!this.open) {
			this.open = true;
			if (index !== this.currentMediaID) {
				if (this.delayedCallToSourceSet) {
					this.delayedCallToSourceSet.kill();
				}
				this.PopulateCopy_(item);
				this.currentMediaID = index;
			} else {
				this.delayedCallToSourceSet.play(0);
			}
			gsap.killTweensOf(this.PopInDOMStructure_.bgOverlay);
			gsap.killTweensOf(this.element_);
			this.DOMMediaWrapper_.classList.remove('visible');
			// Globals.BODY.style.overflow = 'hidden';
			// gsap.to(this.PopInDOMStructure_.bg, {force3D: true, opacity: 1, duration: this.openDuration * 0.1, delay: this.openDuration * 0.5});
			gsap.to(this.PopInDOMStructure_.bgOverlay, { force3D: true, opacity: 1, duration: this.openDuration * 0.25 });
			// gsap.to(this.PopInDOMStructure_.bg, {scale: 1, force3D: true, duration: this.openDuration});
			gsap.to(this.PopInDOMStructure_.bgOverlay, { yPercent: -100, force3D: true, duration: this.openDuration });
			gsap.to(this.PopInDOMStructure_.bgOverlay, {
				scale: 1,
				ease: 'power4.inOut',
				force3D: true,
				duration: this.openDuration,
				onComplete: () => {
					this.DOMMediaWrapper_.classList.add('visible');
				}
			});
			gsap.set(this.element_, { autoAlpha: 1, delay: this.openDuration * 0.25 });
			window.addEventListener('keydown', this.onKeyDown_);
		}
	};

	private PopulateCopy_ = item => {
		this.PopInDOMStructure_.title.innerHTML = item.title;
		this.PopInDOMStructure_.description.innerHTML = item.description;

		this.PopInDOMStructure_.infos.innerHTML = '';
		item.infos.forEach(info => {
			const p = document.createElement('p');
			p.innerHTML = info.description;
			this.PopInDOMStructure_.infos.appendChild(p);
		});

		this.PopInDOMStructure_.ctas.innerHTML = '';
		item.ctas.forEach(cta => {
			const a = document.createElement('a');
			a.href = cta.url;
			const span1 = document.createElement('span');
			span1.classList.add('PrimaryText');
			span1.innerHTML = cta.primaryText;
			a.appendChild(span1);

			if (cta.secondaryText != '') {
				const span2 = document.createElement('span');
				span2.classList.add('SecondaryText');
				span2.innerHTML = cta.secondaryText;
				a.appendChild(span2);
			}

			const productId = cta.productId ? cta.productId : '';
			a.addEventListener('click', e => {
				const target = (e.target as HTMLElement).closest('a');

				const productName = cta.primaryText.replace('Buy ', '').replace(' Now', '');
				tracking.trackFBConversion(productId, productName);
				tracking.trackClick('buy_item_button', 'go_to_site', target.getAttribute('href'));
				tracking.trackEngagement('buy_item_button', productId, productName);
			});

			this.PopInDOMStructure_.ctas.appendChild(a);
		});

		this.DOMMediaWrapper_.innerHTML = '';

		if (item.mediaType === 'sketchFabUrl') {
			this.DOMSketchFabEmbed_.innerHTML = '';
			const iframe = this.createIframe(
				'',
				item.media +
					((item.media as string).endsWith('/embed') || (item.media as string).endsWith('/embed/') ? '' : '/embed') +
					((item.media as string).endsWith('/') ? '' : '/') +
					'?autostart=1&autospin=1&transparent=1&ui_animations=0&ui_infos=0&ui_stop=0&ui_inspector=0&ui_watermark_link=0&ui_watermark=0&ui_hint=0&ui_ar=0&ui_help=0&ui_settings=0&ui_vr=0&ui_fullscreen=0&ui_annotations=0&ui_theme=darkui_color=f53a02'
			);
			this.DOMSketchFabEmbed_.appendChild(iframe);

			this.delayedCallToSourceSet = gsap.delayedCall(this.openDuration, () => {
				this.DOMMediaWrapper_.appendChild(this.DOMSketchFabEmbed_);
				this.DOMMediaWrapper_.classList.add('visible');
			});

			this.element_.classList.add('iframe-mode');
		} else if (item.mediaType === 'sketchFabEmbed') {
			this.DOMSketchFabEmbed_.innerHTML = '';
			const div = document.createElement('div');
			div.innerHTML = item.media;
			const iframe = div.querySelector('iframe');
			this.DOMSketchFabEmbed_.appendChild(iframe);

			this.delayedCallToSourceSet = gsap.delayedCall(this.openDuration, () => {
				this.DOMMediaWrapper_.appendChild(this.DOMSketchFabEmbed_);
				this.DOMMediaWrapper_.classList.add('visible');
			});

			this.element_.classList.add('iframe-mode');
		} else if (item.mediaType === 'image') {
			const img = new Image();
			img.classList.add('lazyload');
			img.sizes = '100vw';
			const fm = Globals.WEBP_SUPPORTED ? 'webp' : 'jpg';

			this.delayedCallToSourceSet = gsap.delayedCall(this.openDuration, () => {
				img.srcset = `${item.media}?w=2000&h=2000&q=80&fm=${fm} 2000w, ${item.media}?w=4000&h=2880&q=80&fm=${fm} 4000w`;
				this.DOMMediaWrapper_.classList.add('visible');
			});

			this.currentImageSize_ = { width: item.width, height: item.height };
			this.currentMediaType_ = 'image';

			const inner = this.DOMImageEmbed_.querySelector('.imageInner') as HTMLDivElement;
			inner.innerHTML = '';
			// inner.style.paddingBottom = (item.height / item.width) * 100 + '%';
			inner.appendChild(img);
			this.DOMMediaWrapper_.appendChild(this.DOMImageEmbed_);
			this.element_.classList.remove('iframe-mode');
		}
	};

	private createIframe = (title, url) => {
		const iframe = document.createElement('iframe') as HTMLIFrameElement;
		iframe.title = '';
		iframe.allowFullscreen = true;
		iframe.allow = 'fullscreen; autoplay;';
		iframe.style.border = 'none';
		iframe.src = url;
		iframe.frameBorder = '0';
		iframe.setAttribute('xr-spatial-tracking', '');
		iframe.setAttribute('execution-while-not-rendered', '');
		iframe.setAttribute('execution-while-out-of-viewport', '');
		iframe.setAttribute('web-share', '');
		iframe.setAttribute('mozallowfullscreen', '');
		iframe.setAttribute('webkitallowfullscreen', '');
		return iframe;
	};
}
