import { gsap } from 'gsap';
import { tracking } from '../../utils/Tracking';

export class Newsletter {
	private form: HTMLFormElement;
	private labelDefault: HTMLElement;
	private successMessage: HTMLElement;
	private errors: HTMLElement;
	private inputEmail: HTMLInputElement;

	constructor(element) {
		this.form = element;
		this.labelDefault = element.querySelector('.Newsletter-title .default');
		this.successMessage = element.querySelector('.Newsletter-title .success');
		this.errors = element.querySelector('.errors');
		this.inputEmail = element.querySelector('.Newsletter-email');

		this.form.addEventListener('submit', this.onSubmit.bind(this));
		this.inputEmail.addEventListener('click', this.hideError.bind(this));
		this.inputEmail.addEventListener('focus', this.hideError.bind(this));
	}

	private hideError = () => {
		this.form.classList.remove('error');
		if (this.errors.querySelector('.active')) {
			this.errors.querySelector('.active').classList.remove('active');
		}
	};

	private showError = (className: string) => {
		this.hideError();
		this.form.classList.remove('success');
		this.form.classList.add('error');
		this.form.querySelector('.error-' + className).classList.add('active');
	};

	private showSuccess = () => {
		const delayMessage = 3;
		this.hideError();

		gsap.set(this.labelDefault, { opacity: 0 });
		gsap.set(this.successMessage, { opacity: 0, display: 'block' });
		gsap.to(this.successMessage, { opacity: 1, display: 'block', duration: 0.4 });

		gsap.delayedCall(delayMessage, () => {
			//this.form.classList.remove('success');
			gsap.to(this.successMessage, { autoAlpha: 0, duration: 0.4, display: 'none' });
			gsap.to(this.labelDefault, { autoAlpha: 1, delay: 0.4, duration: 0.4 });
			this.inputEmail.value = '';
		});
		tracking.trackEngagement('newsletter signup');
	};

	private disableForm = (disable: boolean) => {
		if (disable) {
			this.form.classList.add('disabled');
		} else {
			this.form.classList.remove('disabled');
		}
	};

	private onSubmit = (e: any) => {
		e.preventDefault();
		this.disableForm(true);

		fetch('https://account.staratlas.com/user/register', {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
				Accept: 'application/json'
			},
			body: JSON.stringify({ email: this.inputEmail.value })
		})
			.then(response => {
				this.disableForm(false);
				if (response.status == 200) {
					this.showSuccess();
				} else if (response.status == 409) {
					this.showError('409');
				} else if (response.status == 429) {
					this.showError('429');
				} else {
					this.showError('general');
				}
			})
			.catch(error => {
				this.disableForm(false);
				this.showError('general');
			});
	};
}
