	private scaleTogetherBgs = [/*this.bg, */ this.bgOverlay];

	private newsletter: Newsletter;
	private firstOpen = true;

	private menuDocs = document.querySelectorAll('.MainMenu-Documents');
	private modalOverlay = document.querySelector('#MainMenu-Documents-Modal');
	private modalContent = document.querySelector('#MainMenu-Documents-Modal .modal-content');
	private modalOpen = false;

