import browser from 'browser-detect';
import { AmbientAudio } from '../ui/AmbientAudio';
import { MainScene } from '../ui/MainScene';

declare class DocumentTouch {}

export class GlobalsLanding {
	public static DEBUG: boolean = false;
	public static browserDetect = browser();
	public static SITE_WRAPPER: HTMLDivElement = document.getElementById('App') as HTMLDivElement;
	public static IS_TOUCH_DEVICE: boolean = 'ontouchstart' in window || ((window as any).DocumentTouch && document instanceof DocumentTouch); // https://github.com/Modernizr/Modernizr/blob/master/feature-detects/touchevents.js
	public static SCRUB_DURATION = GlobalsLanding.IS_TOUCH_DEVICE ? 0.1 : 1;
	public static IS_IE: boolean = false;
	public static IS_IOS: boolean = !!navigator.platform && /iPad|iPhone|iPod/.test(navigator.platform);
	//TODO: @lasse Should add check to set safari 14 only to dpi == 1:
	public static RENDERER_PIXEL_RATIO = Math.min(window.devicePixelRatio, GlobalsLanding.IS_IOS ? 2 : 1);
	public static RENDERER_HEIGHT = 0;
	public static CHAPTER_DISTANCE = 201.0252686870402;
	public static SHOW_MARKERS = false;
	public static MainScene: MainScene;
	public static AmbientAudio: AmbientAudio;

	private static DOCUMENT_TOUCH: DocumentTouch = (window as any).DocumentTouch;
	public static IS_MOBILE: boolean = false;
	public static IS_ANDROID: boolean = navigator.userAgent.toLowerCase().indexOf('android') > -1;
	public static IS_SAFARI: boolean = GlobalsLanding.browserDetect && GlobalsLanding.browserDetect.name && GlobalsLanding.browserDetect.name.indexOf('safari') >= 0;
	public static PREMULTIPLY_ALPHA = false; //!(GlobalsLanding.IS_ANDROID || GlobalsLanding.IS_IOS);
	//This causes the sticky overlay to be slightly jumpy in chrome when scrolling using old mices:
	public static USE_STICKY: boolean = true; //GlobalsLanding.IS_IOS || GlobalsLanding.IS_ANDROID || GlobalsLanding.IS_SAFARI;
	public static VIDEO_OPEN: boolean = false;

	// public static browser = detect();
}
