import gsap from 'gsap';
import { Globals } from '../../../utils/Globals';
import { tracking } from '../../../utils/Tracking';

export type MediaType = 'image' | 'sketchFabUrl' | 'sketchFabEmbed';

export interface ItemData {
	el: HTMLDivElement;
	inner: HTMLDivElement;
	hoverDom: HTMLDivElement;
	index: number;
	tag: { title: string; slug: string };
	title: string;
	description: string;
	infos: any[];
	ctas: any[];
	mediaType: MediaType;
	media: string;
	width?: number;
	height?: number;
	distance: number;
	thumbnail: any;
}

export class ShowroomItem {
	public itemData: ItemData;
	private progress = 0;
	private firstLoop = true;
	private tl: any;
	private offset: any;
	private distance: any;
	private CALLBACKOnclick_: any;

	private quickSetter: Function;

	private currentDistance = 0;
	private halfWidth: number;
	private halfHeight: number;

	private pos = { x: 0, y: 0 };
	private mouse = { x: 0, y: 0 };
	private rotationSpeed = 0.02;
	private quickSetterRotationX: Function;
	private quickSetterRotationY: Function;
	private hovering: boolean;
	private aspectRatio: number;
	private dt: number;
	private offsetStart: number;
	public static MAX_ITEMS_TO_SHOW = 4;

	constructor(item, onClickCallback) {
		const dt = item.dataset;
		this.itemData = {
			el: item,
			inner: item.querySelector('.ShowroomInner'),
			hoverDom: item.querySelector('.ShowroomHoverButton'),
			index: parseInt(dt.index),
			tag: {
				slug: dt.tagSlug,
				title: dt.tagTitle
			},
			title: dt.title,
			description: dt.description,
			infos: [],
			ctas: [],
			mediaType: dt.mediaType as MediaType,
			media: dt.media,
			width: parseInt(dt.mediaWidth) ? parseInt(dt.mediaWidth) : 0,
			height: parseInt(dt.mediaHeight) ? parseInt(dt.mediaHeight) : 0,
			distance: 0,
			thumbnail: {
				height: dt.thumbnailHeight,
				width: dt.thumbnailWidth
			}
		};
		this.quickSetter = gsap.quickSetter(item, 'css');
		this.quickSetterRotationX = gsap.quickSetter(this.itemData.inner, 'rotateX', 'deg');
		this.quickSetterRotationY = gsap.quickSetter(this.itemData.inner, 'rotateY', 'deg');
		gsap.set(this.itemData.hoverDom, { autoAlpha: 0 });

		for (let i = 0; i < parseInt(dt.infoLength); i++) {
			this.itemData.infos.push({
				description: dt[`info${i}`]
			});
		}

		for (let i = 0; i < parseInt(dt.ctaLength); i++) {
			this.itemData.ctas.push({
				primaryText: dt[`ctaPrimaryText${i}`],
				secondaryText: dt[`ctaSecondaryText${i}`],
				url: dt[`ctaUrl${i}`],
				productId: dt[`ctaProductId${i}`]
			});
		}

		this.CALLBACKOnclick_ = onClickCallback;
		this.itemData.el.addEventListener('click', this.onClick);
		if (!Globals.IS_TOUCH_DEVICE) {
			this.itemData.el.addEventListener('mousemove', this.mouseMove);
			this.itemData.el.addEventListener('mouseenter', this.mouseHover);
			this.itemData.el.addEventListener('mouseleave', this.mouseLeave);
		}
	}

	private onClick = (event: MouseEvent) => {
		tracking.trackClick('showroom_item', 'open_item');
		event.preventDefault();
		this.CALLBACKOnclick_(this.itemData, this.itemData.index);
		return false;
	};

	public setDistance = (initialPosition: number, index: number, offset: number, perspective: number, fadeInOffset: number, offsetDiff: number, offset1: number) => {
		if (this.tl) this.tl.kill();

		// console.log('ShowroomItem', index, offset, fadeInOffset);
		this.offset = offset;
		this.offsetStart = offset1;
		this.distance = initialPosition;
		gsap.set(this.itemData.inner, { autoAlpha: 0, force3D: true });
		gsap.set(this.itemData.el, { z: -initialPosition, y: 0, zIndex: 1000 - index, pointerEvents: 'none', force3D: true });
		gsap.set(this.itemData.hoverDom, { z: 50, y: -15, autoAlpha: 0, force3D: true });

		this.tl = gsap.timeline({ paused: true });

		// durationInPixel / ( initialDistance + distanceFromZero)
		const fadeInPosition = Math.min(4 * perspective, initialPosition);
		// console.log('fadeInPosition', fadeInPosition);
		// const fadeInPosition = initialPosition

		/*		const opacityFadeInDuration = (fadeInPosition * 0.3) / (fadeInPosition + perspective);
		const opacityFadeInPosition = (fadeInPosition * 0.01) / (fadeInPosition + perspective) + fadeInOffset;
		// console.log('opacityFadeInPosition', opacityFadeInPosition);
		const opacityFadeOutDuration = 500 / (initialPosition + perspective);
		const opacityFadeOutPosition = perspective / (initialPosition + perspective);
		const visibleDur = 1 - opacityFadeOutPosition - (opacityFadeInPosition + opacityFadeInDuration);*/
		// console.log(visibleDur);
		// console.log('opacityFadeOutPosition', opacityFadeOutPosition);

		const visibleDur = fadeInOffset * ShowroomItem.MAX_ITEMS_TO_SHOW;
		let opacityFadeInDuration = fadeInOffset;
		// console.log('opacityFadeInPosition', opacityFadeInPosition);
		let opacityFadeOutDuration = fadeInOffset;
		let opacityFadeInPosition = 1 - (opacityFadeInDuration + opacityFadeOutDuration + visibleDur);
		let opacityFadeOutPosition = 1 - opacityFadeOutDuration;
		let hoverButtonFadeInPosition = opacityFadeInPosition + opacityFadeInDuration + visibleDur * 0.25;
		if (window.innerWidth < 768) {
			opacityFadeOutDuration = fadeInOffset * 0.25;
			opacityFadeInPosition = 1 - (opacityFadeInDuration + fadeInOffset + visibleDur);
			opacityFadeOutPosition = 1 - fadeInOffset;
			hoverButtonFadeInPosition = opacityFadeInPosition + opacityFadeInDuration + visibleDur * 0.75;
		}

		this.tl.set(this.itemData.el, { pointerEvents: '' }, opacityFadeInPosition + opacityFadeInDuration * 0.02);

		this.tl.to(this.itemData.inner, { autoAlpha: 1, duration: opacityFadeInDuration, ease: 'none', force3D: true }, opacityFadeInPosition);
		this.tl.to(this.itemData.inner, { autoAlpha: 0, duration: opacityFadeOutDuration, ease: 'none', force3D: true }, opacityFadeOutPosition);

		this.tl.to(this.itemData.el, { z: perspective, duration: 1, y: 0, ease: 'none', force3D: true }, 0);
		this.tl.to(this, { currentDistance: perspective, duration: 1, ease: 'none' }, 0);
		this.tl.to(
			this.itemData.hoverDom,
			{
				duration: opacityFadeInDuration,
				ease: 'power1.out',
				z: 50,
				y: -15,
				overwrite: 'auto',
				force3D: true
			},
			hoverButtonFadeInPosition
		);
		this.tl.to(this.itemData.hoverDom, { duration: opacityFadeInDuration, autoAlpha: 1, overwrite: 'auto', force3D: true }, hoverButtonFadeInPosition);

		this.tl.to(this.itemData.hoverDom, { duration: opacityFadeOutDuration, ease: 'power1.out', z: 50, y: -15, overwrite: 'auto', force3D: true }, opacityFadeOutPosition);
		this.tl.to(this.itemData.hoverDom, { duration: opacityFadeOutDuration, autoAlpha: 0, overwrite: 'auto', force3D: true }, opacityFadeOutPosition);

		this.tl.set(this.itemData.el, { pointerEvents: 'none' }, opacityFadeOutPosition + opacityFadeOutDuration * 0.95);
		// this.tl.to(this.itemData.el, {zIndex: index, duration: 0.01}, 0.9)
		this.tl.progress(this.offset);
	};

	public update = progress => {
		this.progress = (this.offset /* - this.offsetStart*/ + progress) % 1;

		if (this.tl) {
			this.tl.progress(this.progress);
		}
	};

	public moveItem = (x, y) => {
		const newX = x * Math.min(this.progress, 0.2);
		const newY = y * Math.min(this.progress, 0.2);
		// const newY = -this.distance * this.progress *   2

		// console.log(newY);
		// console.log(this.currentDistance);
		this.quickSetter({ x: newX, y: newY });
	};

	public resize = (valueToScroll, index) => {
		this.halfWidth = this.itemData.el.clientWidth * 0.5;
		this.halfHeight = this.itemData.el.clientHeight * 0.5;
		this.aspectRatio = this.halfWidth / this.halfHeight;
		// this.offset = 150 / valueToScroll * (index + 1)
	};

	public raf = (gsapDelta: number) => {
		this.dt = 1.0 - Math.pow(1.0 - this.rotationSpeed, gsapDelta);
		this.pos.x += (this.mouse.x - this.pos.x) * this.dt;
		this.pos.y += (this.mouse.y - this.pos.y) * this.dt;
		//todo: @lasse, maybe there's a small performance optimization here where we can check if the new value to be set is actually different from the current
		this.quickSetterRotationY(this.pos.x * -4);
		this.quickSetterRotationX(this.pos.y * 4 * this.aspectRatio);
	};

	private mouseMove = (event: MouseEvent) => {
		let coorX = this.halfWidth - Math.max(event.offsetX, 0);
		let coorY = this.halfHeight - Math.max(event.offsetY, 0);

		this.mouse.x = coorX / this.halfWidth;
		this.mouse.y = coorY / this.halfHeight;
		// console.log(coorX, coorY);
		// gsap.to(this.itemData.inner, {duration: 4, rotateX: degX, rotateY: degY, overwrite: "auto"});
	};

	private mouseHover = (event: MouseEvent) => {
		this.hovering = true;
		gsap.to(this.itemData.inner, { duration: 0.75, ease: 'power3.out', scale: 1.08, overwrite: 'auto', force3D: true });
		// gsap.to(this.itemData.hoverDom, { duration: 1, ease: 'power1.out', z: 150, y: -30, overwrite: 'auto' });
		// gsap.to(this.itemData.hoverDom, { duration: 0.5, opacity: 1, overwrite: 'auto' });
		gsap.to(this, { duration: 1, ease: 'power1.out', rotationSpeed: 0.05, overwrite: 'auto' });
	};
	private mouseLeave = (event: MouseEvent) => {
		gsap.to(this.itemData.inner, { duration: 1, ease: 'power3.out', scale: 1, overwrite: 'auto', force3D: true });
		// gsap.to(this.itemData.hoverDom, { duration: 0.75, opacity: 0, overwrite: 'auto' });
		// gsap.to(this.itemData.hoverDom, { duration: 2, ease: 'power3.out', z: 10, y: 0, overwrite: 'auto' });
		gsap.to(this, { duration: 2, ease: 'power3.out', rotationSpeed: 0.02, overwrite: 'auto' });
		this.hovering = false;
		this.mouse.x = 0;
		this.mouse.y = 0;
	};

	public kill = () => {
		this.itemData.el.removeEventListener('click', this.onClick);
		if (!Globals.IS_TOUCH_DEVICE) {
			this.itemData.el.removeEventListener('mousemove', this.mouseMove);
			this.itemData.el.removeEventListener('mouseenter', this.mouseHover);
			this.itemData.el.removeEventListener('mouseleave', this.mouseLeave);
		}
	};
}
