import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { AbstractModule } from '../../../lib/com/hellomonday/templateManager/AbstractModule';
import { Globals } from '../../utils/Globals';
import { ShowroomItem } from './ShowroomItem/ShowroomItem';
import { ShowroomPopIn } from './ShowroomPopIn/ShowroomPopIn';
import { MouseMoveTracker } from '../HomeModule/utils/MouseMoveTracker';
import { InertiaPlugin } from 'gsap/InertiaPlugin';
import { tracking } from '../../utils/Tracking';

interface Tag {
	slug: string;
	title: string;
	count: number;
	el: HTMLElement;
}

gsap.registerPlugin(InertiaPlugin);

export class ShowroomModule extends AbstractModule {
	private DOMShowroomItems: NodeListOf<HTMLDivElement>;
	private items: ShowroomItem[] = [];
	private DOMPopIn: HTMLDivElement;
	private popIn: ShowroomPopIn;
	private DOMScrollSpacer: HTMLElement;
	private total: number;
	private DOMPerspectiveContainer: HTMLElement;
	private config: { verticalAngle: number; perspective: number; perspectiveOrigin: { x: number; y: number; maxGap: number }; spaceBetweenItems: number; spaceBetweenLoops: number };
	private tags: Tag[];
	private DOMFilterList: HTMLElement;
	private DOMFilterItemTemplate: HTMLElement;
	private currentItems: ShowroomItem[];
	private DOMItemContainer: any;
	private scroller: ScrollTrigger;
	private hashChangeTl: gsap.core.Timeline;
	private currentFilter: string;
	private currentTag_: any;
	private easedProgress: number = 0;
	private scrollTween: gsap.core.Tween;
	private mouseMoveTracker = new MouseMoveTracker();
	private loops = 10;
	private scrollLengthFactor = 0.3;
	private proxy = { skew: 0, currentSkew: 0 };
	private skewSetter: Function;
	private rotationClamp = gsap.utils.clamp(-90, 90); // don't let the skew go beyond 20 degrees.
	private tracker = InertiaPlugin.track(this, 'easedProgress')[0];
	private ignoreTracking = false;
	private scrollLine: Element;
	private scrollLineBG: Element;
	private scrollLineDiamond: Element;
	private scrollLineSetter: Function;
	private mouseMoveEnabled: boolean = window.innerWidth >= 768 && this.mouseMoveTracker.isEnabled;
	private mouseMoveFactor: number = 1;
	private currentScrollVelocity: number = 0;
	private gsapDelta: number;
	private dt: number;
	private offsetDiff: number;
	private scrollHelpTween: gsap.core.Tween;
	private scrollSkewFactor = 1;

	constructor(element, module) {
		super(element, module);
		document.documentElement.classList.add('ShowroomTemplatePage');
		document.documentElement.classList.add('noNoise');

		this.DOMShowroomItems = element.querySelectorAll('.ShowroomItem');
		this.DOMPopIn = element.querySelector('.ShowroomPopIn');
		this.DOMScrollSpacer = element.querySelector('.ScrollSpacer');
		this.DOMPerspectiveContainer = element.querySelector('.PerspectiveContainer');
		this.DOMFilterList = element.querySelector('.FilterList');
		this.DOMFilterItemTemplate = this.DOMFilterList.querySelector('.FilterItem');
		this.DOMItemContainer = element.querySelector('.CenterPoint');
		this.scrollLine = element.querySelector('.Showroom-scrollIndicator-line');
		this.scrollLineBG = element.querySelector('.Showroom-scrollIndicator-lineBG');
		this.scrollLineDiamond = element.querySelector('.Showroom-Diamond');

		this.config = {
			spaceBetweenItems: 1500,
			spaceBetweenLoops: -1500,
			verticalAngle: 0,
			perspective: 1500,
			perspectiveOrigin: {
				x: 50,
				y: 0,
				maxGap: 300
			}
		};
		this.tags = [];

		this.DOMShowroomItems.forEach(item => {
			const dt = item.dataset;
			if (dt.tagSlug) {
				const TagExists = this.tags.find(tag => tag.slug === dt.tagSlug);

				if (!TagExists) {
					this.tags.push(<Tag>{ slug: dt.tagSlug, title: dt.tagTitle, count: 1, el: null });
				} else {
					TagExists.count += 1;
				}
			}

			this.items.push(new ShowroomItem(item, this.onItemClick));
		});

		this.skewSetter = gsap.quickSetter(this.DOMShowroomItems, 'rotateX', 'deg');
		this.scrollLineSetter = gsap.quickSetter(this.scrollLine, 'scaleY');

		this.currentItems = this.items;
		this.popIn = new ShowroomPopIn(this.DOMPopIn);
		this.bindEvents();

		if (this.tags.length > 0) {
			this.createTags();
		}

		this.onHashChange();
		this.setupScroll();
	}

	private createTags = () => {
		this.DOMFilterList.innerHTML = '';
		this.tags.unshift({ slug: 'all', title: 'Everything', count: 1, el: null });
		this.tags.forEach(tag => {
			const DOMTag = this.createSingleTag(tag.slug, tag.title);
			tag.el = DOMTag;
			this.DOMFilterList.appendChild(DOMTag);
		});
	};

	private createSingleTag = (slug, title) => {
		const clone = this.DOMFilterItemTemplate.cloneNode(true) as any;
		clone.dataset.slug = slug;
		const DOMLink = clone.querySelector('a');
		DOMLink.href = '#' + slug;
		DOMLink.innerText = title;

		DOMLink.addEventListener('click', this.onFilterClick.bind(this));

		return clone;
	};

	private onFilterClick = e => {
		const target = (e.target as HTMLElement).closest('a');

		tracking.trackClick('showroom_filter_item', 'filter_items', target.getAttribute('href'));
		if (window.innerWidth < 768) {
			this.toggleFilterList(e.target.parentNode, e);
		}
	};

	private toggleFilterList = (filterItem: HTMLElement, e: any) => {
		const duration: number = 0.2;
		if (this.DOMFilterList.classList.contains('open')) {
			this.tags.forEach(tag => {
				if (filterItem === tag.el) tag.el.classList.add('is-active');
				else tag.el.classList.remove('is-active');
			});

			gsap.to(this.DOMFilterList, {
				opacity: 0,
				duration,
				onComplete: () => {
					this.DOMFilterList.classList.remove('open');
					this.tags.forEach(tag => (tag.el.style.display = ''));
					gsap.to(this.DOMFilterList, { opacity: 1, duration });
				}
			});
		} else {
			e.preventDefault();
			this.tags.forEach(tag => (tag.el.style.display = 'block'));
			gsap.set(this.DOMFilterList, { opacity: 0, duration });
			gsap.to(this.DOMFilterList, {
				opacity: 1,
				duration,
				onComplete: () => {
					this.DOMFilterList.classList.add('open');
				}
			});
		}
	};

	private bindEvents = () => {
		window.addEventListener('hashchange', this.onHashChange);
		gsap.ticker.add(this.raf);
	};

	private onHashChange = () => {
		let hash = window.location.hash.replace('#', '');
		hash = !hash ? 'all' : hash;
		const filteredItems = this.items.filter(({ itemData }) => itemData.tag.slug == hash);
		const tag = this.tags.find(({ slug }) => slug == hash) as Tag;

		if (tag) {
			tag.el.classList.add('is-active');

			if (this.currentTag_) {
				this.currentTag_.el.classList.remove('is-active');
			}
			this.currentTag_ = tag;
		}

		if (this.hashChangeTl) this.hashChangeTl.kill();
		let tempItems = null;

		if (filteredItems.length > 0) {
			tempItems = filteredItems;
		} else {
			tempItems = this.items;
		}

		if (!this.currentFilter) {
			// console.log('No previously active filter');
			this.setFilteredItems(tempItems);
		} else {
			// console.log('Previously active filter: ', this.currentFilter);
			this.hashChangeTl = gsap.timeline();
			this.hashChangeTl.to(
				window,
				{
					scrollTo: { y: 0, autoKill: false },
					duration: 0.3,
					onComplete: () => {
						ScrollTrigger.refresh();
					}
				},
				0
			);
			this.hashChangeTl.to(this.DOMPerspectiveContainer, { opacity: 0, duration: 0.4 }, 0);
			this.hashChangeTl.add(() => this.setFilteredItems(tempItems), 0.4);
			this.hashChangeTl.to(this.DOMPerspectiveContainer, { autoAlpha: 1, duration: 0.4 });
		}
		this.currentFilter = hash;
	};

	private setFilteredItems = (tempItems: ShowroomItem[]) => {
		this.DOMItemContainer.innerHTML = '';
		this.currentItems = tempItems;
		// console.log(tempItems.length);
		ShowroomItem.MAX_ITEMS_TO_SHOW = gsap.utils.clamp(2, 4, tempItems.length - 2);
		// InertiaPlugin.untrack(this, 'easedProgress');
		this.ignoreTracking = true;
		this.tracker.remove('easedProgress');
		if (this.scrollTween) {
			// console.log(this.scrollTween.scrollTrigger);
			this.scrollTween.scrollTrigger.getTween().progress(1);
		}
		tempItems.forEach(({ itemData }) => {
			this.DOMItemContainer.appendChild(itemData.el);
		});
		this.setItemsPositions();
		// this.tracker = null;
		this.easedProgress = 0;
		this.currentScrollVelocity = 0;
		this.proxy.currentSkew = 0;
		this.proxy.skew = 0;
		this.tracker.add('easedProgress');
		setTimeout(() => {
			this.ignoreTracking = false;
			//according to gsap documentation the tracker needs at least 100ms to track properly:
			//"Then, after at least 100ms and 2 “ticks” of the core engine have elapsed (so that some data has been recorded), you can create throwProps tweens for those properties and omit the velocity values and it will automatically populate them for you internally. For example:"
		}, 100);
		this.onUpdate();
	};

	private onItemClick = (item, index) => {
		this.popIn.openPopIn(item, index);
	};

	private setupScroll = () => {
		this.scrollTween = gsap.to(this, {
			easedProgress: 1,
			ease: 'none',
			scrollTrigger: {
				scrub: Globals.IS_TOUCH_DEVICE ? 0.1 : 1
				// onUpdate: this.onScrollTriggerUpdate
			},
			onUpdate: this.onUpdate
		});
		this.onUpdate();
		this.scrollHelpTween = gsap.to(this.element.querySelector('.ScrollHelpIndicator'), {
			opacity: 0,
			yPercent: 100,
			ease: 'none',
			scrollTrigger: {
				scrub: Globals.IS_TOUCH_DEVICE ? 0.1 : 1,
				end: 200
			}
		});
	};

	private setItemsPositions = () => {
		this.total = this.config.spaceBetweenItems * this.currentItems.length + this.config.spaceBetweenLoops;
		this.scrollSkewFactor = this.currentItems.length / 4;
		let offsetSpace = 0;
		if (this.currentItems.length === 1) {
			this.total = this.config.spaceBetweenItems * this.currentItems.length;
			offsetSpace = -0.25;
		} else if (this.currentItems.length === 2) {
			offsetSpace = -0.25;
		} else {
			offsetSpace = this.currentItems.length > 2 ? this.config.spaceBetweenLoops / this.total : 0;
		}
		const travelLength = this.total + this.config.spaceBetweenItems;
		// Define where each timeline should start
		const offset = this.config.spaceBetweenItems / this.total;
		const offset2 = this.config.spaceBetweenItems / travelLength;
		this.offsetDiff = offset - offset2;
		const fadeInOffset = 1 / this.currentItems.length; //0; //(1 - ((this.maxItemsToShow + 1 ) / this.currentItems.length));
		// console.log('offsetSpace', offsetSpace);
		// console.log('offset', offset);
		// console.log('offset2', offset2);
		// console.log('offsetDiff', offset2);
		// console.log('travelLength', travelLength);
		// console.log('setItemsPositions', this.total, this.currentItems.length, fadeInOffset);
		//10500 0.14285714285714285 0
		//6000 0.25 0
		this.currentItems.forEach((item, index) =>
			item.setDistance(this.total, index, (this.currentItems.length - 1 - index) * offset2 + offsetSpace + this.offsetDiff, this.config.perspective, fadeInOffset, this.offsetDiff, offset)
		);
		this.DOMScrollSpacer.style.height = this.total * this.loops * this.scrollLengthFactor + window.innerHeight + 'px';
		ScrollTrigger.refresh();
	};

	private onUpdate = () => {
		// if (Math.abs(skew) > Math.abs(this.proxy.skew)) {
		// 	gsap.to(this.proxy, {skew: 0, duration: 0.8, ease: "power3", overwrite: true, onUpdate: () => this.skewSetter(this.proxy.skew)});
		// }
		// this.scrollLineSetter(this.easedProgress);
		this.scrollLineSetter((this.easedProgress * this.loops) % 1);
		this.currentItems.forEach(item => item.update(this.easedProgress * this.loops));
	};

	kill = () => {
		super.kill();
		window.removeEventListener('hashchange', this.onHashChange);
		if (this.scrollTween) {
			this.scrollTween.kill();
			this.scrollTween = null;
		}
		if (this.scrollHelpTween) {
			this.scrollHelpTween.kill();
			this.scrollHelpTween = null;
		}
		gsap.ticker.remove(this.raf);
		this.currentItems.forEach(item => item.kill());
		document.documentElement.classList.remove('ShowroomTemplatePage');
		document.documentElement.classList.remove('noNoise');
	};

	resize() {
		super.resize();
		this.DOMScrollSpacer.style.height = this.total * this.loops * this.scrollLengthFactor + window.innerHeight + 'px';
		this.items.forEach((item, index) => item.resize(this.total, index));
		this.mouseMoveEnabled = window.innerWidth >= 768 && this.mouseMoveTracker.isEnabled;
		this.mouseMoveFactor = window.innerWidth / 1440;
		if (!this.mouseMoveEnabled) {
			this.items.forEach(item => item.moveItem(0, 0));
		}

		if (window.innerWidth >= 768) {
			this.tags.forEach(tag => (tag.el.style.display = ''));
			this.DOMFilterList.classList.remove('open');
			this.DOMFilterList.style.opacity = '1';
		}
	}

	private rotationSpeed = 0.1;
	private raf = () => {
		this.mouseMoveTracker.raf();
		if (!this.ignoreTracking) {
			this.currentScrollVelocity = this.tracker.get('easedProgress');
		}
		this.gsapDelta = gsap.ticker.deltaRatio();
		// console.log(this.currentScrollVelocity);
		this.proxy.skew = this.rotationClamp(this.currentScrollVelocity * this.scrollSkewFactor * 100);
		// adjust speed for higher refresh monitors
		this.dt = 1.0 - Math.pow(1.0 - this.rotationSpeed, this.gsapDelta);

		this.proxy.currentSkew += (this.proxy.skew - this.proxy.currentSkew) * this.dt;

		this.skewSetter(this.proxy.currentSkew);

		this.items.forEach(item => item.raf(this.gsapDelta));
		if (this.mouseMoveEnabled) {
			this.items.forEach(item => item.moveItem(this.mouseMoveTracker.pos.xEased * 400 * this.mouseMoveFactor, this.mouseMoveTracker.pos.yEased * 250 * this.mouseMoveFactor));
		}
	};
}
